	module.exports = {"data":{"markdownRemark":{"html":"<p>I Norges rike musikktradisjon er Eldbjørg Hemsing en av de fremste utøverne, og navnet hennes har vært kjent i hjemlandet fra hun som 11-åring hadde sin solodebut med Bergen Filharmoniske Orkester. Hun studerte med Boris Kuschnir i Wien, og etter den tid har hun gjort seg gradvis sterkere bemerket både på den nasjonale og den internasjonale scenen, mens hun har perfeksjonert framføringsstilen og skapt seg et repertoar som spenner fra Beethoven til Dvořák, fra Grieg til Tan Dun. Hun har samarbeidet med Tan Dun om en mengde prosjekter både i Europa og i Asia, og for tiden arbeider hun sammen med komponisten med et nytt verk som skal ha premiere i 2018.</p>\n<p>I mars 2018 gir Eldbjørg Hemsing ut debut-cd-en sin på det svenske plateselskapet BIS, med fiolinkonserter av Hjalmar Borgström og Dmitrij Sjostakovitsj, innspilt med Wiener Symphoniker og Olari Elts. Da Eldbjørg Hemsing introduserte albumet, skrev hun:</p>\n<blockquote>\n<p>«For noen få år siden ble jeg kjent med musikken til Hjalmar Borgström, et navn som tidligere var ukjent for meg. Jeg ble overrasket da jeg fant ut at han hadde vært en velkjent komponist og kritiker i Norge i begynnelsen av det 20. århundret. Da jeg åpnet partituret til fiolinkonserten hans, ble jeg straks fengslet. Denne konserten, som ble skrevet i 1914, er ufattelig vakker, full av norsk nasjonalfølelse som var så typisk for den tiden, men også verdt internasjonal oppmerksomhet. Musikken minner meg om stedet jeg kommer fra – det ville landskapet i Valdres og Jotunheimen der fjellene reiser seg dramatisk over dalførene, og musikken får meg til å lengte etter røttene mine. Etter at Borgström døde i 1925, ble konserten helt glemt, så i dag ser jeg det som en oppgave å gjøre det jeg kan for å gjøre musikken hans kjent på ny.»</p>\n</blockquote>\n<p>Høsten 2018 kommer Hemsing med en oppfølger til debutalbumet. Det er en innspilling av Dvořáks fiolinkonsert og Suks «Fantasy» og «Love Song», innspilt med Antwerp Symphony Orchestra og Alan Buribayev. Hun er også hovedperson i en kommende dokumentar laget av David Donnelly, der han setter søkelys på kvinnelige kunstnere. Dette programmet følger opp den prisvinnende suksessen som NRK laget i 2010, der Eldbjørg og søsteren Ragnhild utforsket livet til den legendariske norske fiolinisten Ole Bull.</p>\n<p>I sesongen 2017/2018 skal Eldbjørg Hemsing ha faste framføringer i Stormen Konserthus i Bodø, der hun i 2018 er «Artist in Residence». Videre skal hun blant annet ha framføringer med Oslo-Filharmonien, MDR Sinfonieorchester Leipzig, Baltic Sea Philharmonic, Arctic Philharmonic og NWD Philharmonie. Hun skal også på en konsertturné med Julien Quentin våren 2018, og opptrer som gjestesolist på Paavo Järvis Pärnu Festival i Estland.</p>\n<p>Eldbjørg Hemsing spiller på en G.B. Guadagnini-fiolin fra 1754 som er utlånt fra Dextra Musica Foundation. Til langsiktig kunstnerlig utvikling har hun fått støtte fra Göhde Foundation.</p>\n<p>Foto: Nikolaj Lund.</p>","timeToRead":2,"excerpt":"I Norges rike musikktradisjon er Eldbjørg Hemsing en av de fremste utøverne, og navnet hennes har vært kjent i hjemlandet fra hun som 1…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/artister/eldbjorg-hemsing.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":null,"composedin":null,"startDate":null,"startTime":null,"location":null,"artists":null,"medialink":"https://www.youtube.com/embed/T7C3YrptAVY","title":"Eldbjørg Hemsing","dedicatedTo":null,"author":null,"date":"2018-05-07T12:00:00.048Z","category":"artister","tags":["fiolinist"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/EldbjorgHemsing-48ee89c4cc9999e55230d768c6aed219-47774.jpg","srcSet":"/static/EldbjorgHemsing-48ee89c4cc9999e55230d768c6aed219-45ad6.jpg 480w,\n/static/EldbjorgHemsing-48ee89c4cc9999e55230d768c6aed219-e2fcf.jpg 960w,\n/static/EldbjorgHemsing-48ee89c4cc9999e55230d768c6aed219-47774.jpg 1920w,\n/static/EldbjorgHemsing-48ee89c4cc9999e55230d768c6aed219-93088.jpg 2880w,\n/static/EldbjorgHemsing-48ee89c4cc9999e55230d768c6aed219-bfc0d.jpg 3840w,\n/static/EldbjorgHemsing-48ee89c4cc9999e55230d768c6aed219-290d4.jpg 4500w","sizes":"(max-width: 1920px) 100vw, 1920px"}}}},"fields":{"slug":"/artister/eldbjorg-hemsing/","artist":null,"opuses":[{"frontmatter":{"title":"13","category":"opus","workname":"Fiolinsonate nr. 2 i G-dur","thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/opus13-50739954f69aec4513f076184d5bb8af-466fa.jpg","srcSet":"/static/opus13-50739954f69aec4513f076184d5bb8af-27b23.jpg 90w,\n/static/opus13-50739954f69aec4513f076184d5bb8af-0afbd.jpg 180w,\n/static/opus13-50739954f69aec4513f076184d5bb8af-466fa.jpg 360w,\n/static/opus13-50739954f69aec4513f076184d5bb8af-43088.jpg 540w,\n/static/opus13-50739954f69aec4513f076184d5bb8af-545bd.jpg 720w,\n/static/opus13-50739954f69aec4513f076184d5bb8af-8c1c4.jpg 1080w,\n/static/opus13-50739954f69aec4513f076184d5bb8af-1a344.jpg 1920w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/opus13/","artist":[{"frontmatter":{"title":"Eldbjørg Hemsing","tags":["fiolinist"]}},{"frontmatter":{"title":"Christian Ihle Hadland","tags":["pianist"]}}]}}]}}},"pathContext":{"slug":"/artister/eldbjorg-hemsing/"}}

